.forms-container {
  display: flex;
  justify-content: space-between;
  padding: 40px;
  margin: 0 auto;
  max-width: $container-small;
  background-image: linear-gradient(to bottom, $blueLight, $blueDark);
  border-radius: 20px;

  .form-item {
    width: 49%; } }


.zano-form-widget {
  border: 4px solid transparent; }

.zano-form-widget.active {
  border-color: $button-gradient-one!important; }
