.footer-nav {
  margin: 60px 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;

  &__section {
    margin: 0 40px;
    text-align: left; }


  &__title {
    color: white;
    font-size: 13px;
    letter-spacing: 1.2px; }

  &__el {
    margin-top: 14px; }

  &__link {
    color: $text-main;
    letter-spacing: 0!important;
    font-weight: 400!important;
    text-transform: capitalize!important; }

  &__link.disabled {
    opacity: 0.5;
    pointer-events: none; } }
