.logo {
  position: relative;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  text-decoration: none;

  &__image {
    display: inline-block;
    background-image: url(data:image/svg+xml;base64,);
    width: 50px;
    height: 50px;
    transition: transform 1s ease-in-out; }

  &__text {
    display: block;
    margin-left: 14px;
    text-transform: uppercase;
    text-align: left;
    font-weight: 700;
    font-size: 12px;
    letter-spacing: 1px;
    color: $text-controls;

    @include m($tablet) {
      display: none; } }

  &__world {
    display: block;
    line-height: 1.6; }

  &__world:last-child {
    color: #7cabee; } }

.logo:hover .logo__image {
  transform: rotateY(180deg); }
